.cc-switch {

  .cc-switch__with-label {
    margin-left: 8px;
    color: var(--body-text-default);
    font-family: var(--family-sans-serif, proxima-nova, sans-serif);
    font-size: var(--font-size-body);

    .cc-switch__label {
      display: inline;
      cursor: pointer;

      &:hover {
        color: var(--switch-checked-color);
      }
    }
  }
}
